@font-face {
  font-family: "mali";
  src: url(../fonts/mali.ttf) format("truetype"); }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

body {
  font-family: "mali", arial, helvetica, sans-serif; }

h1 {
  color: #2d8fba; }

.image {
  width: 200px;
  height: 200px;
  object-fit: cover;
  background: url(../images/cat.jpg);
  background-position: center;
  background-size: 100%;
  color: white; }

